.snow-logo {
  height: 102px;
  width: 240px;
  /* background-color: #13A5F7; */
  font-family: HelveticaNeueCyr;
  line-height: 20px;
  font-size: 35px;
  color: #FFFFFF;
  background-image: url(data:image/png;base64,);
}
.news-feed {
  width: 240px;
}

.news-feed-list {
  border: 1px solid black;
}

.news-feed-title {
  font-family: HelveticaNeueCyr;
  line-height: 14px;
  font-size: 14px;
  letter-spacing: 4px;
  text-transform: uppercase;
  font-weight: bold;
  color: #2A2A2A;
}

.news-feed-item {
  border-bottom: 1px solid #000000;
  color: #2A2A2A;
}

.news-feed-item:hover {
  color: #13A5F7;
  cursor: pointer;
}

.news-feed-item .date {
  font-family: HelveticaNeueCyr;
  line-height: 12px;
  font-size: 12px;
  text-transform: uppercase;
  color: #13A5F7;
}

.news-feed-item .title {
  font-family: Georgia;
  font-size: 14px;
}
.header-main {
  font-family: HelveticaNeueCyr;
  background-color: #2A2A2A;
  /* height: 51px; */
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #B4B4B4;
}

.header-social {
  font-size: 14px;
  color: #B4B4B4;
}

.header-social-item {
  display: inline-flex;
  align-items: center;
  /* height: 30px; */
  padding: 8px 0;
  width: 30px;
  border-radius: 50%;
  color: #B4B4B4;
}

.header-social-item:hover {
  text-decoration: none;
  background-color: #333333;
  cursor: pointer;
  color: #4dabf7;
}

.header-hamburger, .header-search {
  font-size: 18px;
  height: 51px;
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #ffffff;
  border-left: 1px solid #464646;
}

.header-hamburger:hover,
.header-search:hover {
  color: #4dabf7;
}

.header-main .caret-green {
  color: #0B9000;
}

.header-main .caret-red {
  color: #FF0000;
}
.link {
  font-family: HelveticaNeueCyr;
  font-size: 11px;
}

.link:hover {
  text-decoration: none;
}
.header-news {
  height: 51px;
  background-color: #ffffff;
  box-shadow: -34px 0 30px -16px rgba(0, 0, 0, 0.15) inset
}

.header-news .header-news-wrapper {
  width: auto;
  overflow-x: auto;
}

.header-news .link {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #949494;
}

.header-news .link:hover {
  border-bottom: 2px solid #13A5F7;
  color: #2A2A2A;
}
.news-item {

}

.news-item .placeholder {
  left: 20px;
  bottom: 20px;
  font-family: HelveticaNeueCyr;
  line-height: 32px;
  font-size: 27px;
  color: #FFFFFF;
  font-weight: bold;
}

.news-block .news-item-large .preview-text {
  font-family: Georgia;
  font-style: normal;
  font-weight: normal;
  line-height: 26px;
  font-size: 18px;
  color: #2A2A2A;
}
.news-block {

}

.news-block .title {
  font-family: HelveticaNeueCyr;
  font-size: 14px;
  letter-spacing: 7px;
  text-transform: uppercase;
  color: #2A2A2A;
  font-weight: bold;
}

.news-block .date {
  font-family: HelveticaNeueCyr;
  line-height: 19px;
  font-size: 11px;
  text-transform: uppercase;
  color: #B4B4B4;
} 

.news-block .type {
  font-family: HelveticaNeueCyr;
  line-height: 16px;
  font-size: 11px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #13A5F7;
}

.news-block .preview-text {
  font-family: HelveticaNeueCyr;
  line-height: 24px;
  font-size: 22px;
  color: #2A2A2A;
  font-weight: bold;
}

.news-block .article-text {
  font-family: Georgia;
  font-style: normal;
  font-weight: normal;
  line-height: 26px;
  font-size: 18px;
  color: #2A2A2A;
}

.news-block .title-line {
  border-bottom: 1px solid #EFEFEF;
}

.news-block .news-more {
  font-family: HelveticaNeueCyr;
  line-height: 16px;
  font-size: 16px;
  text-align: center;
}

.news-block .link:hover .preview-text {
  color: #13A5F7;
}

.news-block .editor-choise {
  background-color: #F8F8F8;
}

.news-block .editor-choise-title {
  font-family: HelveticaNeueCyr;
  line-height: 14px;
  font-size: 14px;
  letter-spacing: 4px;
  text-transform: uppercase;
  font-weight: bold;
  color: #2A2A2A;
}

.news-block .test-btn {
  font-family: HelveticaNeueCyr;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: bold;
  color: #FFFFFF;
  background: #13A5F7;
  border-color: #13A5F7;
  border-radius: 3px;
}
.footer {
  font-family: HelveticaNeueCyr;
}

.footer-subscribe {
  background-color: #333333;
}

.footer-subscribe-title {
  font-family: HelveticaNeueCyr;
  line-height: 32px;
  font-size: 20px;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
}

.footer-subscribe-block {
  width: 468px;
  height: 48px;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
}

.footer-subscribe-block input {
  height: 100%;
  font-family: HelveticaNeueCyr;
  line-height: 16px;
  font-size: 14px;
  color: #B4B4B4;
  width: calc(100% - 48px);
  border-radius: 3px;
  border: 0;
}

.footer-subscribe-block span {
  color: white;
  cursor: pointer;
  background-color: #13A5F7;
  width: 48px;
  height: 48px;
  display: flex;
  font-size: 20px;
  align-items: center;
  justify-content: center;
}

.footer-info {
  background-color: #2A2A2A;
}

.footer-info-text {
  font-family: HelveticaNeueCyr;
  font-size: 12px;
  color: #8F8F8F;
}

.footer-logo {
  font-family: HelveticaNeueCyr;
  line-height: 20px;
  font-size: 20px;
  font-weight: bold;
}

.foonter-links-title {
  line-height: 15px;
  font-size: 11px;
  text-transform: uppercase;

  color: #8C8C8C;
}

.home {
  width: 970px;
}
html, body {
  height: 100%;
  overflow: hidden;
  margin: 0;
}
#root, .snow, .snow-col {
  height: 100%;
}

.snow-col-feed, .snow-col-main {
  height: 100%;
  overflow-y: scroll;
}

